export default function FunctionGlobal() {
  window.formatCurrency = function (number) {
    if (isNaN(number)) {
      return 'Invalid number'
    }

    let parts = parseFloat(number).toFixed(2).split('.')
    let formattedNumber = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, '.')

    if (parts[1] === '00') {
      return formattedNumber + ' '
    } else {
      return formattedNumber + ',' + parts[1] + ' '
    }
  }

  window.convertDateFormat = function (dateString) {
    // To mt i tng Date t chui ngy
    const date = new Date(dateString)

    // Ly ngy, thng v nm t i tng Date
    const day = date.getDate().toString().padStart(2, '0') // m bo ngy c 2 ch s
    const month = (date.getMonth() + 1).toString().padStart(2, '0') // Thng c nh s t 0 n 11
    const year = date.getFullYear()

    // Tr v chui ngy theo nh dng "dd-mm-yyyy"
    return `${day}-${month}-${year}`
  }
}
